<template>
	<div id="login_div">
		<el-row :gutter="12">
      <el-col :span="8" :offset="8">
        <el-card shadow="always">
          <div id="login_card">
            <img src="@/assets/register_logo.png">
            <el-input v-model="email" placeholder="Email"></el-input>
            <el-input placeholder="Password" v-model="password" show-password></el-input>
            <div class="error"><span>{{password_message}}</span></div>
            <el-button round @click="check">Login</el-button>
          </div>
        </el-card>
      </el-col>
    </el-row>
	</div>
</template>

<script>
  import axios from "axios"
  function validateEmail(email) 
{
    var re = /\S+@\S+\.\S+/;
    return re.test(email);
}
export default {
  data() {
    return {
      email: '',
      email_message:'',
      password:'',
      password_message:''
    }
  },
  methods: {
    check() {
      const email = this.email;
      const password = this.password;
      if (email == '' || password == '') {
        this.$message({
          message: "Please Enter Email & Password",
          type: "error"
        })
      } else if(!validateEmail(email)) {
        this.$message({
          message: "Please Enter Correct Email Address",
          type: "error"
        })
      } else {
          axios.post('http://35.197.160.94:8094/user/login', {
            email_address: email,
            Password: password
          })
          .then((response) => {
            if (response.status === 200 && response.data.token != undefined && response.data.token.length > 1) {
              this.$store.dispatch('setTokenData', response.data.token)
              console.log(response.data.token)
              this.$store.dispatch('setUserData', response.data.uid)
              this.$message({
                message: 'Welcome',
                type: 'success'
              })
              this.$router.push({path: '/Dashboard/Overview'})
            }
            else {
              this.$message({
                message: 'Wrong Email or Password',
                type: 'error'
              })
            }
          })
          .catch(function (error) {
            console.log(error)
          })
      }
    },
  }
}
</script>

<style>
  #login_div {
    margin-top: 180px;
  }
  #login_card {
    text-align: center;
  }
  #login_card img {
    height: 140px;
  }
  #login_card .el-input {
    margin-top: 20px;
  }
  #login_card .el-input__inner {
    width: 70%;
    background-color: white !important;
    border-width: 0px 0px 1px 0px;
  }
  #login_card .el-button {
    margin-top: 20px;
    width: 50%;
    background-color: #178de7;
    color: #fff;
  }
  #login_card .error {
    color: red;
  }
</style>



